.footer {
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: center;
  max-width: 320px;
  margin-right: auto;
  margin-left: auto;
}
.footer__container {
  width: 280px;
  margin-left: auto;
  margin-right: auto;
}
.footer__text {
  display: inline;
  color: #545454;
  font-size: 14px;
  line-height: 1.17em;
  margin-bottom: 10px;
  text-align: center;
}
.footer__text--secondary {
  margin: 0%;
}
.footer__icon {
  position: relative;
  bottom: -2px;
  width: 14px;
  height: 13px;
}
.footer__link {
  color: #545454;
  font-size: 14px;
  line-height: 1.17em;
  border-bottom: solid 1px #545454;
}

.footer__link {
  text-decoration: none;
  background-image: linear-gradient(rgb(176, 251, 188), rgb(176, 251, 188)),
    linear-gradient(#feb2b2, #feb2b2);
  background-size: 100% 2px, 0 2px;
  background-position: 100% 100%, 0 100%;
  background-repeat: no-repeat;
  transition: background-size 2s linear;
}

.footer__link:hover {
  background-size: 0 2px, 100% 2px;
  cursor: pointer;
}

.footer-modal__content-hidden {
  display: none;
}

.footer-modal__overlay {
  display: none;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
  overflow: hidden auto;
  padding: 50px;
  box-sizing: border-box;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
}

.footer-modal__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 320px;
  width: 100%;
  max-width: 700px;
  min-height: 100%;
  margin: 0 auto;
}

.footer-modal__block {
  display: block;
  width: 100%;
  min-height: 100px;
  border-radius: 5px;
  padding: 10px;
  box-sizing: border-box;
  background: #fff;
  position: relative;
  background-color: white;
}

