.footer__btn {
  display: inline-block;
  height: 44px;
  width: 112px;
  border-radius: 5px;

  font-size: 12px;
  line-height: 1.33;

  background-color: transparent;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;

  border: 1px solid #000000;
  color: #000000;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    border: none;
    color: #ffffff;
    background-color: #ff6b01;
    box-shadow: drop-shadow(0px 8px 43px rgba(255, 107, 1, 0.6));
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
  }
}

.footer__modal-title {
  color: #ff6b01;

  font-weight: 700;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.03em;
}

.team__list {
  padding: 10px;
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
  }
}

.team__item {
  @media screen and (max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }
  @media screen and (min-width: 768px) {
    flex-basis: calc(100% / 2 - 30px);
    margin: 15px;
  }

  @media screen and (min-width: 1024px) {
    flex-basis: calc(100% / 4 - 30px);
  }
}

.person {
  width: 100%;
  padding: 5px;
  border-radius: 0px 0px 4px 4px;
  background-color: lightgrey;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}

.person__name {
  margin-top: 0;
  color: black;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.19;
  text-align: center;
  letter-spacing: 0.03em;

  @media screen and (max-width: 767px) {
    font-size: 18px;
  }

  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
}

.person__photo {
  width: 120px;
  height: 150px;
}
