.header__button__list__item__title {
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.56);
  width: 129.53px;
  height: 44px;
  cursor: pointer;
  border: 1px solid #FFFFFF;
  text-align: center; 

  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 1.333;

  color: #FFFFFF;

    &:hover,
    &:focus {
      background-color: $accent-color;
  }

}
  



