@media screen and (min-width: 768px) {
  .header__button__list__item{
    width: 129.53px;
    height: 44px;
  }
}

@media screen and (min-width: 1024px) {
  .header__button__list__item{
    width: 129.53px;
    height: 44px;
  }
}


.header__button__list__item:not(:last-child) {
  margin-right: 20px;
}


